<template>
  <div class="scroller">
    <div class="header">
      <h2>{{title}}</h2>
      <a href="#">更多</a>
    </div>
    <div class="content">
      <slot name="promItem"></slot>
      <ul class="hasCover" v-if="type === 'hasCover'">
        <li v-for="item in items">
          <router-link :to="'subject/' + item.id" append>
            <img :src="item.images.large">
            <span class="title">{{item.title}}</span>
            <span class="rank">
              <span class="star-full"></span>
              <span class="star-full"></span>
              <span class="star-full"></span>
              <span class="star-gray"></span>
              <span class="star-gray"></span>
              <span>6.9</span>
            </span>
          </router-link>
        </li>
      </ul>
      <ul class="onlyString" v-if="type === 'onlyString'">
        <li v-for="item in items" style="border-color: #FFAC2D;">
          <a :href="item.href" v-if="!item.line" :style="{color: item.color}">{{item.title}}</a>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
    export default {
        name: 'scroller',
        props: ['title', 'type', 'items'],
        data () {
          return {

          }
        }
    };
</script>

<style lang="scss" rel="stylesheet/scss" scoped>
  .scroller {
    padding-top: 1rem;
  }
  .header {
    height: 2.6rem;
    line-height: 2.6rem;
    padding: 0 1.6rem;

    a {
      float: right;
      font-size: 1.44rem;
      &:last-child {
        color: #42bd56;
      }
    }

    h2 {
      display: inline-block;
      margin: 0;
    }
  }
  .content {
    box-sizing: content-box;

    ul {
      padding: 0.8rem 0;
    }
  }


  .hasCover {
    overflow-x: auto;
    white-space: nowrap;
    text-align: center;
    .title {
      display: block;
      max-width: 100%;
      margin-top: 1rem;
      font-size: 1.6rem;
      line-height: 1.6rem;
      color: #111;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
      word-wrap: normal;
    }
    li {
      display: inline-block;
      width: 10rem;
      margin-left: 1rem;
    }
    li:first-child {
      padding-left: 0.8rem;
    }
    img {
      height: 15rem;
    }
    .rank {
      margin-top: 0.4rem;
    }
    .star-full {
      display: inline-block;
      width: 1rem;
      height: 1rem;
      background-image: url(data:image/png;base64,);
      background-size: 1rem 1rem;
    }
    .star-gray {
      display: inline-block;
      width: 1rem;
      height: 1rem;
      background-image: url(data:image/png;base64,);
      background-size: 1rem 1rem;
    }
    .rank span:last-child {
      font-size: 1.4rem;
      color: #aaa;
    }
  }
  .onlyString {
    overflow-x: auto;
    white-space: nowrap;
    li {
      display: inline-block;
      margin: 0 0 0.8rem 1.6rem;
      font-size: 1.6rem;
      border: solid 0.1rem;
      border-radius: 0.4rem;
      vertical-align: middle;
    }
    a {
      height: 5rem;
      line-height: 5rem;
      padding: 0 2.4rem;
      letter-spacing: 0.16rem;
      overflow: auto;
      display: block;
      text-align: center;
    }
    li:empty {
      width: 100%;
      display: block;
      height: 0.1rem;
      border: 0;
      margin: 0;
    }
  }

</style>



