<template>
  <div class="header-bar">
    <h1 class="title">
      <router-link to="/pages/home">
        豆瓣
      </router-link>
    </h1>
    <ul class="nav">
      <li>
        <router-link style="color: #2384E8" to="/pages/movie">
          电影
        </router-link>
      </li>
      <li>
        <router-link style="color: #9F7860" to="/pages/book">
          图书
        </router-link>
      </li>
      <li>
        <router-link style="color: #E4A813" to="/pages/status">
          广播
        </router-link>
      </li>
      <li>
        <router-link style="color: #2AB8CC" to="/pages/group">
          小组
        </router-link>
      </li>
    </ul>
    <span class="search" @click="showSearch"></span>
  </div>

</template>

<script>
    export default {
      name: 'header-bar',
      data () {
        return {
        }
      },
      methods: {
        showSearch: function () {
          this.$emit('showSearch')
        }
      }
    };
</script>

<style lang="scss" rel="stylesheet/scss" scoped>
  .header-bar {
    .title {
      flex: 1;
      max-width: 4.6rem;
      max-height: 2.2rem;
      margin-right: 1.8rem;
      font-size: 0;
      line-height: 3.2rem;
      color: #00b600;
      word-break: break-all;
      background: url(data:image/png;base64,) no-repeat;
      background-size: cover;

      a {
        display: block;
      }
    }

    ul {
      display: flex;
      flex: 1;
      justify-content: flex-end;
    }

    li {
      display: inline-block;
      font-size: 1.6rem;
      margin-right: 1.2rem;
    }

    .search {
      font-size: 0;
      background: url(data:image/png;base64,) no-repeat;
      background-size: cover;
      width: 2.4rem;
      height: 1.8rem;
      margin-top: 0.4rem;
    }
  }

</style>



